import React, { useContext } from 'react';
import EditorContext from '../../contexts/Editor';
import Button from '../Button';
import Separator from '../Separator';

export default () => {
  const {
    addText, addRect, addCircle, addImage, saveData
  } = useContext(EditorContext);

  return (
    <div className="tools">
      <Button onClick={addText}>Dodaj tekst</Button>
      <Button onClick={addRect}>Dodaj prostokt</Button>
      <Button onClick={addCircle}>Dodaj koo</Button>
      <Button onClick={addImage}>Dodaj grafik</Button>
      <Button onClick={saveData}>Zapisz</Button>

      <Separator />
    </div>
  );
};
